@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Jura:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightblue;
  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* HEADER ---------------------------------------------------- */
header {
    background: linear-gradient( to right bottom,rgb(47, 123, 211), rgb(63, 62, 62));
    width: 100%;
  }
  
  header h1 {
    margin-left: 15px;
    color: rgb(255, 255, 255);
    font-family: 'Jura', sans-serif;
  }
  
  /* HERO ---------------------------------------------------- */
  
  .hero-container {
    margin: 20px;
    width: 450px;
  }
  
  .hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 20px;
    padding: 10px;
    border: 1px solid #ccc;
    box-shadow: 5px 5px 4px rgb(63, 63, 63);
    text-transform: uppercase;
    font-family: 'Josefin Sans', sans-serif;
    height: 100%;
    
  }
  
  .button-container-hero button {
    padding: 6px;
    margin: 4px;
    cursor: pointer;
  }
  
  
  
  
  
  
  /* FORM ---------------------------------------------------- */
  .form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: 'Josefin Sans', sans-serif;
  }
  
  .form-container input {
    padding: 5px;
    margin: 0 5px 5px 5px;
    width: 160px;
  }
  
  .form-container button {
    width: 100%;
    padding: 3px;
    margin-bottom: 15px;
    font-size: 14px;
    text-align: center;
  }
  
  
  .input-container-form {
    margin-bottom: 5px;
  }
  
  .color-form-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
  }
  
  .color-form-input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 15px;
  }
  
  .color-form-input input {
    width: 10%;
  }
  
  .color-form-container select {
    padding: 5px;
    margin-bottom: 10px;
  }
.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  width: 560px;
  margin-top: 40px;
  
}

.app button {
  text-transform: uppercase;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 11px;
}


